<template>
    <div class="container">
        <TopButton title="" />
        <div v-if="isLoading">  ...</div>
        <div v-else>
            <div class="reviewTop">
                <div class="search-container">
                    <input
                        type="text"
                        v-model="searchQuery"
                        placeholder="  "
                        class="search-box"
                        @keyup.enter="applySearch"
                    />
                    <button @click="applySearch" class="search-button"></button>
                </div>

                <div v-if="filteredPosts.length > 0">
                    <!--    -->
                    <div v-for="post in filteredPosts" :key="post.id" class="post-item">
                        <PostItem :post="post" @postDeleted="removePost" />
                    </div>
                </div>
                <div v-else class="no-posts">  .</div>

                <div class="review-bt-container">
                    <button class="reviewBtn" type="button" @click="navigateToCreate"> ></button>
                </div>
            </div>
            <!--   -->
            <div class="contents">
                <div
                    class="reviewItem"
                    v-for="review in filteredPosts"
                    :key="review.id"
                    @click="navigateToDetail(review.user_id)"
                >
                    <div class="reviewImg">
                        <img :src="review.image_path" alt=" " v-if="review.image_path" />
                    </div>
                    <div class="reviewContent">
                        <p class="title">{{ review.title }}</p>
                        <p class="content_r">{{ review.content }}</p>
                        <p class="score"><img src="@/assets/img/leaf.svg" alt="score" /> 4.3</p>
                        <p>{{ review.username || '' }}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div class="margin"></div>
</template>

<script>
import TopButton from '@/components/TopButton.vue';

export default {
    name: 'ReviewList',
    components: {
        TopButton
    },
    data() {
        return {
            reviews: [], //   
            isLoading: false,
            filteredPosts: [], //    
            searchQuery: ''
        };
    },
    methods: {
        //   
        async fetchReviews() {
            this.isLoading = true; //  
            try {
                const response = await fetch('http://green609b.dothome.co.kr/api/posts/fetch_reviews.php');
                if (!response.ok) {
                    throw new Error(`    .  : ${response.status}`);
                }
                this.reviews = await response.json();
                this.filteredPosts = this.reviews; //    
            } catch (error) {
                console.error('     :', error);
                alert('    .  .');
            } finally {
                this.isLoading = false; //  
            }
        },
        //    
        navigateToCreate() {
            this.$router.push('/reviews/create');
        },
        //    
        navigateToDetail(reviewId) {
            this.$router.push(`/reviews/${reviewId}`);
        },
        //   
        applySearch() {
            const query = this.searchQuery.toLowerCase().trim();
            if (query) {
                //    
                this.filteredPosts = this.reviews.filter(
                    (review) => review.title && review.title.toLowerCase().includes(query)
                );
            } else {
                //     
                this.filteredPosts = this.reviews;
            }
        }
    },
    mounted() {
        this.fetchReviews(); //      
    }
};
</script>

<style scoped>
/*  */
.contents {
    width: 100%;
    margin: 0 auto;
    overflow: hidden;
}
.reviewTop {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 0 15px 0;
}
.search-container {
    display: flex;
    align-items: center;
}
.search-container input {
    outline: none;
    height: 29px;
    border: 1px solid #b8b8b8;
    font-size: 0.9em;
}
.search-button {
    border: 1px solid #a68151;
    height: 29px;
    font-size: 0.8rem;
    color: #f3f3f3;
    width: 45px;
    background-color: #a68151;
    font-weight: bold;
    letter-spacing: -0.1px;
    transition: all 0.3s;
}
.search-button:hover {
    background: #8d6c40;
}
.reviewBtn {
    border: none;
    background: none;
    font-size: 0.8rem;
    padding: 4px 7px;
    color: #a68151;
    font-weight: bold;
    box-sizing: border-box;
    border-radius: 5px;
    letter-spacing: -0.1px;
    transition: all 0.3s;
}
.reviewBtn:hover {
    color: #8d6c40;
}

.reviewItem {
    width: 98%;
    padding: 15px 0;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: transform 0.2s;
    border-bottom: 1px solid #ccc;
}
.reviewItem:hover {
    transform: scale(1.02);
}
.reviewImg {
    width: 125px;
    height: 150px;
    margin-right: 20px;
}
.reviewImg img {
    display: block;
    width: 125px;
    height: 150px;
    object-fit: cover;
}
.reviewContent {
    width: 100%;
    float: left;
    text-align: left;
}
.reviewContent p {
    margin: 0;
    padding-right: 20px;
    font-size: 0.85rem;
}

p.title {
    width: 63vw;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 1.2rem;
    font-weight: bold;
    line-height: 2rem;
}

p.content_r {
    width: 63vw;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 0.85rem;
    line-height: 1.2rem;
}

.score img {
    margin-right: 3px;
    filter: invert(10%) sepia(89%) saturate(6058%) hue-rotate(83deg) brightness(108%) contrast(86%);
}

p.score {
    font-size: 0.9rem;
    line-height: 2.5rem;
}

.margin {
    height: 63px;
}
</style>
